<template>
  <div id="wrap">
    <div class="select" @click="openPopup('operateSlots')">
      <mt-cell title="车辆是否运营" is-link>
        <span>{{operate?operate: '请选择'}}</span>
      </mt-cell>
    </div>
    <div class="select" @click="openPopup('serviceSlots')">
      <mt-cell title="服务礼包" is-link>
        <span>{{service?service: '请选择服务礼包'}}</span>
      </mt-cell>
    </div>
    <div class="container" style="margin-bottom:48px;">  
      <div class="des" v-if="selfChoose">
        <div class="mint-radiolist" v-for="(item,index) in radioArr">
          <label class="mint-radiolist-title" style="font-size:14px;">{{item.key}}</label>
          <a class="mint-cell" v-for="(radioItem, radioIndex) in item.value" @click="check()" :price="radioItem.price" :id="radioItem.id">
            <div class="mint-cell-wrapper" style="background-image:none;margin-bottom: 10px;">
              <div class="mint-cell-title">
                <label class="mint-radiolist-label">
                  <span class="mint-radio">
                    <input type="radio" class="mint-radio-input" :value= "radioItem.id">
                    <span class="mint-radio-core"></span>
                  </span>
                  <span class="mint-radio-label">{{radioItem.name}}</span>
                  <span class="mint-price">{{radioItem.price}}</span>
                  <span class="mint-unit">{{radioItem.unit}}</span>
                </label>
                <div style="padding:5px 0 5px 28px;">
                  <span>服务说明：</span>
                  <span>{{radioItem.explain}}</span>
                </div>
              </div>
            </div>
          </a>
        </div>
      </div>
       <div class="des" v-else>
        <h4>描述：</h4>
        <p v-html="des"></p>
      </div>  
    </div>
    <div class="footer">
      <span>服务价格</span>
      <span class="price">{{price}}</span>
      <span class="unit">{{unit}}</span>
      <mt-button type="primary" @click="save">保存并返回</mt-button>
    </div>
    <mt-popup v-model="popupVisible" position="bottom" class="mint-popup">
      <mt-picker :slots="slots" show-toolbar ref="areaPicker">
        <mt-button @click="handleCancel">取消</mt-button>
        <mt-button @click="handleConfirm">确认</mt-button>
      </mt-picker>
    </mt-popup>
  </div>
</template>
<script>
export default {
  data() {
    return {
      popupVisible: false,
      type: '',
      value: '',
      operate: '否',
      service: '',
      des: '',
      price: '',
      unit: '',
      slots: [],
      selfChoose: false,
      title: '车辆保险',
      selectData: {},
      getService: {},
      operateSlots: [{
        values: ['否', '是']
      }],
      serviceSlots: [],
      radioArr: [],
      checkedArr: []
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      document.title = '增值服务'
    })
  },
  computed: {
    getCarInfo() {
      return this.$store.getters['GET_CAR_INFO']
    },
    getServiceInfo() {
      return this.$store.getters['GET_SERVICE_INFO']
    }
  },
  mounted() {
    this.operate = this.getServiceInfo.is_open === '是,否' ? '否' : this.getServiceInfo.is_open
    this.service = this.getServiceInfo.name
    this.des = this.getServiceInfo.explain
    this.price = this.getServiceInfo.price
    this.unit = this.getServiceInfo.unit
    this.selectData.car_type = this.getCarInfo.car_type
    this.selectData.price_mode = this.getServiceInfo.choosePriceMode
    var that = this
    if (this.getServiceInfo.name === '自选服务') {
      this.selfChoose = true
      this.selectData.is_open = this.getServiceInfo.is_open
      this.$axios({
        url: this.$baseUrl + '?r=interfaces/car-valuation/get-self-service-data',
        method: 'post',
        data: this.selectData,
        transformRequest: [function (data) {
          var ret = '';
          for (var it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(res => {
        if (res.data.errcode === 0) {
          that.radioArr = res.data.data
          that.$nextTick(function () {
            if (that.getServiceInfo.checkedArr && that.getServiceInfo.checkedArr.length) {
              that.getServiceInfo.checkedArr.forEach(function (v, i) {
                $('#' + v).addClass('isCheck')
                $('#' + v).find('.mint-radio-input')[0].checked = true
              })
            }
          })
        } else {
          that.$toast(res.data.msg)
        }
      })
    }
  },
  methods: {
    check() {
      let selfPrice = 0;
      $(event.currentTarget.parentNode).find('.mint-radio-input').forEach((item) => {
        item.checked = false
      })
      $(event.currentTarget.parentNode).find('.mint-cell').removeClass('isCheck')
      $(event.currentTarget).find('.mint-radio-input')[0].checked = true
      $(event.currentTarget).addClass('isCheck')
      $('.isCheck').each(function (i, v) {
        selfPrice += $(v).attr('price') * 1
      })
      this.price = selfPrice
    },
    openPopup(type) {
      var that = this
      this.type = type
      switch (type) {
        case 'operateSlots':
          this.slots = this.operateSlots
          this.popupVisible = true
          break
        case 'serviceSlots':
          this.selectData.is_open = this.operate
          this.$axios({
            url: this.$baseUrl + '?r=interfaces/car-valuation/get-service-combobox',
            method: 'post',
            data: this.selectData,
            transformRequest: [function (data) {
              var ret = '';
              for (var it in data) {
                ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
              }
              return ret
            }],
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded'
            }
          }).then(res => {
            if (res.data.errcode === 0) {
              let areaArr = []
              let areaObj = {};
              let citySlotsArr = [];
              if (res.data.data && res.data.data.length) {
                res.data.data.forEach(function (v, i) {
                  areaArr.push(v.name)
                })
              }
              areaObj.values = areaArr
              citySlotsArr.push(areaObj)
              that.serviceSlots = citySlotsArr
              that.slots = that.serviceSlots
            } else {
              this.$toast(res.data.msg)
            }
            this.popupVisible = true
          })
          break
      }
    },
    handleCancel() {
      this.popupVisible = false
    },
    handleConfirm() {
      var that = this
      switch (this.type) {
        case 'operateSlots':
          this.operate = this.$refs.areaPicker.getValues()[0]
          this.popupVisible = false
          break
        case 'serviceSlots':
          if (this.$refs.areaPicker.getValues()[0] === '自选服务') {
            this.$axios({
              url: this.$baseUrl + '?r=interfaces/car-valuation/get-self-service-data',
              method: 'post',
              data: this.selectData,
              transformRequest: [function (data) {
                var ret = '';
                for (var it in data) {
                  ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
                }
                return ret
              }],
              headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
              }
            }).then(res => {
              if (res.data.errcode === 0) {
                that.price = ''
                that.radioArr = res.data.data
                that.selfChoose = true
                if (res.data.data[0].value[0].unit) {
                  that.unit = res.data.data[0].value[0].unit
                }
              } else {
                that.$toast(res.data.msg)
              }
            })
          } else {
            this.getService = this.selectData
            this.getService.name = this.$refs.areaPicker.getValues()[0]
            this.$axios({
              url: this.$baseUrl + '?r=interfaces/car-valuation/get-service-data',
              method: 'post',
              data: this.getService,
              transformRequest: [function (data) {
                var ret = '';
                for (var it in data) {
                  ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
                }
                return ret
              }],
              headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
              }
            }).then(res => {
              if (res.data.errcode === 0) {
                that.des = res.data.data[0].explain
                that.price = res.data.data[0].price
                that.unit = res.data.data[0].unit
                that.selfChoose = false
              } else {
                that.$toast(res.data.msg)
              }
            })
          }
          this.service = this.$refs.areaPicker.getValues()[0]
          this.popupVisible = false
          break
      }
    },
    save() {
      if (!this.price) {
        this.$toast('请选择')
        return
      }
      let that = this;
      this.getServiceInfo.is_open = this.operate
      this.getServiceInfo.price = this.price
      this.getServiceInfo.unit = this.unit
      this.getServiceInfo.name = this.service
      this.checkedArr = []
      $('.isCheck').each(function (i, v) {
        that.checkedArr.push($(v).attr('id'))
      })
      this.getServiceInfo.checkedArr = this.checkedArr
      this.$router.push('./')
    }
  }
}
</script>
<style scoped>
.mint-popup {
  width: 100%;
}

.mint-cell {
  font-size: 12px;
  min-height: 40px;
}

.des {
  font-size: 14px;
}

.footer {
  position: fixed;
  bottom: 0px;
  font-size: 16px;
  width: 100%;
  line-height: 38px;
  z-index: 999;
  background-color: #fff;
  padding: 5px 0;
}

.footer .price {
  color: red;
  margin-left: 20px;
}

.footer .unit {
  margin-left: 5px;
}

.footer > button {
  border-radius: 0;
  height: 38px;
  font-size: 14px;
  float: right;
  margin-right: 20px;
  width: 120px;
}
.mint-radiolist-label {
  display: flex;
  align-items: center;
  padding: 0;
}
.mint-radiolist-label .mint-radio-label {
  flex: 1;
}

.mint-radiolist-label .mint-price {
  margin-right: 5px;
}
</style>



